	module.exports = {"data":{"markdownRemark":{"fileAbsolutePath":"/home/luanorlandi/git/angeloocana/src/pages/blog/static-site/setup-ssl.fr.md","html":"<p>Comment configurer <strong>https://</strong> en utilisant <strong>surge.sh</strong> et <strong>ssl.com</strong>.\nC’était un peu difficile de rassembler toutes les pièces, j’espère que je peux aider toute personne en difficulté.</p>\n<p>Vous pouvez suivre le didacticiel officiel ici <a href=\"https://surge.sh/help/securing-your-custom-domain-with-ssl\">https://surge.sh/help/securing-your-custom-domain-with-ssl</a></p>\n<p>Mais reste ici, j’ai fait des choses assez différentes et c’est axé sur <strong>ssl.com</strong></p>\n<h2>Générer le fichier .pem</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">cat</span> angeloocana_com.key angeloocana_com.crt <span class=\"token string\">'SSL.com DV CA.crt'</span> <span class=\"token operator\">></span> surge3.pem\n</code></pre>\n      </div>\n<p>J’ai eu des problèmes avec la <strong>chaîne</strong>, mon https fonctionnait dans Chrome, mais pas dans Firefox.\nLorsque j’ai testé sur sslshopper.com, j’ai eu cette erreur:</p>\n<blockquote>\n<p>The certificate is not trusted in all web browsers.\nYou may need to install an Intermediate/chain certificate to link it to a trusted root certificate.\nLearn more about this error. The fastest way to fix this problem is to contact your SSL provider.\n<cite>sslshopper</cite></p>\n</blockquote>\n<p>J’ai résolu cela en ajoutant <strong>‘SSL.com DV CA.crt’</strong> comme troisième fichier dans la génération de fichiers .pem.</p>\n<h2>Télécharger le fichier .pem sur surge.sh</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    surge ssl\n</code></pre>\n      </div>\n<h2>Testez vos paramètres</h2>\n<p>ssl tools est un bon outil pour tester vos paramètres ssl et télécharger les fichiers manquants:\n<a href=\"http://www.ssltools.com/\">http://www.ssltools.com/</a></p>","excerpt":"Comment configurer  https://  en utilisant  surge.sh  et  ssl.com .\nC’était un peu difficile de rassembler toutes les pièces, j’espère que…","fields":{"tagSlugs":[{"tag":"Gatsby","link":"/fr/tags/gatsby/"},{"tag":"surge.sh","link":"/fr/tags/surge-sh/"},{"tag":"ssl.com","link":"/fr/tags/ssl-com/"},{"tag":"ssl","link":"/fr/tags/ssl/"},{"tag":"https","link":"/fr/tags/https/"}],"slug":"/fr/blog/static-site/setup-ssl/","readNextPosts":[{"excerpt":"Avez-vous besoin de modifier des fichiers dans Word et Excel sous Linux? Suivez mes étapes pour installer LibreOffice, un paquet de…","frontmatter":{"title":"Comment utiliser Word et Excel dans Linux ou peut-être pas...","date":"2017-08-30"},"fields":{"slug":"/fr/blog/linux/arch/install-libre-office/","langKey":"fr"}},{"excerpt":"Move your font files to  /usr/share/fonts/","frontmatter":{"title":"Install fonts in Arch linux","date":"2017-07-31"},"fields":{"slug":"/fr/blog/linux/arch/install-fonts/","langKey":"fr"}},{"excerpt":"Cette erreur se produit lorsque vous regardez de nombreux fichiers dans linux. Vous pouvez recevoir un message d’erreur comme ceci: error…","frontmatter":{"title":"Correction de l'erreur ENOSPC","date":"2017-09-03"},"fields":{"slug":"/fr/blog/linux/arch/fix-enospc-error/","langKey":"fr"}}]},"frontmatter":{"youtubeId":null,"title":"Comment configurer ssl https:// pour votre site","tags":["Gatsby","surge.sh","ssl.com","ssl","https"],"date":"2017-08-30","structuredData":{"type":"TechArticle","author":"angeloocana","dependencies":"Hosting at Surge.sh","proficiencyLevel":"Beginner","articleSection":"static-site","pageEnd":null,"pageStart":null,"pagination":null,"about":{"name":"ssl","alternateName":"https","description":"SSL (Secure Sockets Layer) is the https:// thing you see in some urls, it adds security technology for establishing an encrypted link between a web server and a browser.","identifier":"ssl","image":null,"sameAs":"https://en.wikipedia.org/wiki/Transport_Layer_Security"},"accessMode":"textual","accessModeSufficient":"textual","accessibilityAPI":"ARIA","accessibilityControl":"fullKeyboardControl, fullTouchControl, fullMouseControl","accessibilitySummary":null,"aggregateRating":"...","audience":"...","alternativeHeadline":"Setup ssl","comment":"...","commentCount":"...","contentLocation":"...","dateCreated":"2017-08-26","dateModified":"2017-08-30","datePublished":"2017-08-30","discussionUrl":"...","educationalUse":"...","isAccessibleForFree":true,"isFamilyFriendly":true,"keywords":"...","locationCreated":"Barueri BR","thumbnailUrl":"...","version":1,"video":"..."}}}},"pathContext":{"path":"/fr/blog/static-site/setup-ssl/","langKey":"fr"}}

